import React, { Component } from 'react';


class Singelucard extends Component {

  render() {

    return (

      <div className="card">
      <img class="card-img-top" src={this.props.vacations.image} />
      <div className="card-body">
        <h5 className="card-title">{this.props.vacations.destination}</h5>
        <p className="card-text">{this.props.vacations.description}</p>
        <ul className="list-group list-group-flush">
            <li className="list-group-item">
                <p><label className="fas fa-plane-departure plane-icon"></label>{this.props.vacations.start_date}</p>
                <p><label className="fas fa-plane-arrival plane-icon"></label>{this.props.vacations.end_date}</p>
            </li>
            <li className="list-group-item">{this.props.vacations.price}<label className="fas fa-dollar-sign dollar-icon"></label></li>
            <li className="list-group-item"><label className="fas fa-eye eye-icon">{this.props.vacations.followers}</label></li>
        </ul>
      </div>

      </div>

    );

  }
  
}

export default Singelucard;